// import React from 'react';
import Navebar from '../Home/Navebar';
import { CartProvider, useCart } from 'react-use-cart';
import ScrollToTop from 'react-scroll-to-top';

function Cart() {
    const {
        isEmpty,
        totalUniqueItems,
        items,
        updateItemQuantity,
        removeItem,
        emptyCart,
        totalItems,
        cartTotal,
    } = useCart();

    if (isEmpty) return <p>Your cart is empty</p>;

    return (
        <div>
            <h2 className="text-center">Cart ({totalUniqueItems})
                <button className="btn btn-danger"
                    onClick={() => emptyCart()}>Delete All
                </button> </h2>
            <div className="container">
                <div className="row">
                    <div className="col-md-10 ">

                        <table className="table table-dark">
                            <thead>
                                <tr>
                                    <th>Image</th>
                                    <th>Name</th>
                                    <th>Quantity</th>
                                    <th>Prics</th>
                                    <th>Operation</th>

                                </tr>
                            </thead>
                            <tbody>
                                {items.map((item) => (
                                    <tr key={item.id}>
                                        <th><img src={item.images[0]} style={{ width: "60px", height: "60px" }}></img></th>
                                        <td><h4>{item.title}</h4></td>
                                        <td><h5>{item.quantity}</h5></td>
                                        <td><h5>{item.price * item.quantity}</h5></td>
                                        <td>
                                            <button className="btn btn-success"
                                                onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) - 1)}
                                            >
                                                -
                                            </button>
                                            <button className="btn btn-danger mx-2"
                                                onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) + 1)}
                                            >
                                                +
                                            </button>
                                            <button className="btn btn-warning"
                                                onClick={() => removeItem(item.id)}>&times;</button>
                                        </td>
                                    </tr>

                                ))}
                                <tr>
                                    <td>total</td>
                                    <td>----</td>
                                    <td>{totalItems} </td>
                                    <td>{cartTotal}</td>
                                    <td></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    );
}


function Allcart() {
    return (
        <CartProvider>
            <Navebar />
            <br />
            <br />
            <br />
            <Cart />
            <ScrollToTop smooth />

        </CartProvider>

    )
}


export default Allcart
