import { render } from "@testing-library/react";
import axios from "axios";
import { Component } from "react";
import Navebar from "../Home/Navebar";
import { CartProvider, useCart } from "react-use-cart";
import ScrollToTop from "react-scroll-to-top";

class Product3 extends Component {
    state = {
        person3: []
    }

    componentDidMount() {
        axios.get("https://forkify-api.herokuapp.com/api/search?q=pizza").
            then((item) => {
                console.log(item);
                this.setState(
                    {
                        person3: item.data.recipes
                    }
                )
            })
    }

    render() {
        return (
            <CartProvider>
                <Navebar />
                <div class="container">
                    <div class="row d-flex flex-row ">
                        {this.state.person3.map((element) =>
                            <div class="col-md-4 col-lg-4  ">
                                <img src=
                                    {element.image_url} alt="" class="img-fluid" style={{width:"300px", height:"300px" }} ></img>
                                <h4>{element.recipe_id}</h4>
                                <h5>{element.title}</h5>
                            </div>
                        )}
                    </div>
                </div>
                <ScrollToTop smooth />
      
            </CartProvider>
        )
    }
}
export default Product3;
